class QuizSection extends React.Component {
  constructor (props) {
    super(props);
    this.state = {
      currentQuestion: 0,
      constentIsLoaded: false,
      error: null,
      jsonData: null,
      completed: false,
    }
    this.chosed = this.chosed.bind(this);
    this.result = 0;
    this.handleClick = this.handleClick.bind(this);
  }
  handleClick (e) {
    if (e.target.classList.contains('try-again')) {
      this.setState({completed: false, currentQuestion: 0});
      this.result = 0;
    }
  }
  check (value) {
    if (value) this.result++;
    if (this.state.currentQuestion >= this.state.jsonData.length-1) {
      console.log(this.result);
      return true;
    }
  }
  chosed(e) {
    if (e.target.classList.contains('quiz-input')) {
      e.target.checked = false;
      if (this.check(this.state.jsonData[this.state.currentQuestion].answers[+e.target.id].isRight)) {
        this.setState({completed: true});
        console.log('finished');
      }
      else {
        this.setState({currentQuestion: this.state.currentQuestion + 1});
      }
    }
  }
  componentDidMount() {
    fetch(requsetUrl + 'quiz/questions.json').then(data => data.json()).then((data) => {
      this.setState({jsonData: data, constentIsLoaded: true});
    },
    error => {
      this.setState({error: error});
    }
    )
  }
  render() {
    let quizElement;
    if (this.state.error) {
      quizElement = <h3 className="message-text">Під час завантаження сталася помилка</h3>
    }
    else if (this.state.constentIsLoaded && this.state.jsonData && !this.state.completed) {
      quizElement = 
      <div className="quiz-box">
        <p className="question-text">{this.state.jsonData[this.state.currentQuestion].question}</p>
        <div className="answers-field">
        {this.state.jsonData[this.state.currentQuestion].photo && <div className="quiz-photo-wrapper"> <img className="quiz-img" style={{opacity: 0}} onLoad={(e) => e.target.style.opacity = 1} alt=" " src={requsetUrl + '/quiz/photos/' + this.state.jsonData[this.state.currentQuestion].photo} /> </div>}
        <form onSubmit={(e) => e.preventDefault()}>
          {this.state.jsonData[this.state.currentQuestion].answers.map((el, i) => <label key={i} className="answer"> <input id={i} className="quiz-input" name="question" type="radio" /> {el.text}</label>)}
        </form>

        </div>
      </div>
    }
    else if (this.state.completed) {
      const result = this.result / this.state.jsonData.length;
      quizElement = 
      <div className="finished">
        <h2 className="finished-main-text">Вітаю тест завершено!</h2>
        <p className="text">Ось твій результат:</p>
        <p className="result-text">{this.result + '/' + this.state.jsonData.length}</p>
        <div className="success-bar">
          <div style={{width:result*100+'%'}} className="success-scale"></div>
        </div>
        <button onClick={this.handleClick} className="try-again">Пройти ще раз</button>
      </div>
    }
    else if (!this.state.constentIsLoaded) {
      quizElement = <h3>Loading...</h3>
    }
    return (
    <section className="quiz-section" onChange={this.chosed} id="test">
      <h2 className="section-title">Тест на знання козацтва</h2>
      {quizElement}
    </section>
    )
  }
}

class VideoSection extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      isLoaded: false,
      error: null
    }
  }
  componentDidMount() {
    fetch(requsetUrl + 'videos-list.json').then(data => data.json()).then(data => {
      this.setState({data, isLoaded: true})
    },
    error => {
      this.setState({error});
    });
  }
  
  render() {
    let element;
    if (this.state.error) {
      element = <h3 className="message-text">Під час завантаження сталася помилка</h3>
    }
    else if (this.state.data && this.state.isLoaded) {
      element = this.state.data.map((el, i) => <iframe key={i} title={'Video-' + i} width="560" height="315" src={el} frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>)
    }
    return(
      <section className="video-section" id="video">
        <h2 className="section-title">Відео</h2>
        <div className="video-container">
          {element}
        </div>
      </section>
    )
  }
}

